                "light-green",
                "deep-orange",
                "blue-grey"
            ],



        }

    },
    methods: {
        getColor(status) {
            if (status == "Delayed") return 'red'
            else return 'green'
        },
    },

    computed: {
        columns() {
            if (this.$vuetify.breakpoint.xl) {
